
.notifications {
  position: fixed;
  top: 50px;
  right: 0;
  z-index: 1257;
  pointer-events: none;
}
@media screen and (min-width: 769px), print {
.notifications {
      max-width: 320px;
}
}
.notifications .notification {
    margin: 20px;
}
.notification {
  position: relative;
  min-width: 240px;
  backface-visibility: hidden;
  transform: translate3d(0, 0, 0);
  pointer-events: all;
}



