.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
html {
  background-color: whitesmoke;
}
.nprogress-container {
  position: fixed !important;
  width: 100%;
  height: 50px;
  pointer-events: none;
}
.loginactionbar {
  margin: 10px;
}
.loginstate {
  margin: 5px;
}
.CONNECTED_FONT_COLOR {
  color: #93c47d;
}
.CONNECTED {
  background: #93c47d;
}
.CONNECTED_COLOR {
  color: #93c47d;
  font-size: 50px;
}
.DISCONNECTED_FONT_COLOR {
  color: #ff6666;
}
.invalid {
  color: #ff6666;
}
.DISCONNECTED {
  background: #ff6666;
}
.DISCONNECTED_COLOR {
  color: #ff6666;
  font-size: 50px;
}
.ON_PROGRESS_FONT_COLOR {
  color: #f5d164;
}
.ON_PROGRESS {
  background: #f5d164;
}
.ON_PROGRESS_COLOR {
  color: #f5d164;
  font-size: 50px;
}
.fa-hdd-o {
  font-size: 30px;
}
.login {
  position: absolute;
  top: 40%;
  left: 40%;
}



