
@charset "UTF-8";
.breadcrumb {
  --separator: "\2044";
  list-style: none;
  align-items: center;
  display: flex;
  justify-content: flex-end;
}
.breadcrumb > li + li:before {
    padding: 0 5px;
    color: #ccc;
    content: var(--separator, "⁄");
}



